       name=\"vendorCategories\"\n                                    id=\"vendorCategories\"\n                                    v-model=\"catId\"\n                                    multiple\n                                >\n                                    <option hidden disabled selected>\n                                        Select vendor’s category\n                                    </option>\n                                    <option\n                                        v-for=\"(\n                                            item, index\n                                        ) in defaultCategories\"\n                                        :key=\"index\"\n                                        :value=\"item.id\"\n                                    >\n                                        {{ item.name }}\n                                    </option>\n                                </select>\n                            </label>\n                            <div>\n                                <span\n                                    v-for=\"(item, index) in catId\"\n                                    :key=\"index\"\n                                >\n                                    {{\n                                        defaultCategories.find(\n                                            (x) => x.id === item\n                                        ).name\n                                    }}\n                                </span>\n                            </div>\n                            <!-- <label for=\"vendorLocationLink\">\n                                <input\n                                    type=\"text\"\n                                    name=\"map\"\n                                    id=\"vendorLocationLink\"\n                                    placeholder=\"Add location link\"\n                                    v-model=\"sendProfile.map\"\n                                />\n                                <button id=\"location-link\">\n                                    <img\n                                        src=\"/assets/vendors/dashboard/location-icon.svg\"\n                                        alt=\"\"\n                                    />\n                                    Insert Link\n                                </button>\n                            </label> -->\n                        </div>\n                    </div>\n                </div>\n            </div>\n            <div id=\"ven-profile-form-submit\">\n                <button type=\"submit\">Submit</button>\n            </div>\n        </form>\n    </div>\n</template>\n\n<script>\nexport default {\n    props: [\"vendorId\"],\n    data() {\n        return {\n            sendCategory: [],\n            catId: [],\n            errors: null,\n            defaultCategories: null,\n        };\n    },\n    methods: {\n        formSubmit(e) {\n            var _this = this;\n            console.log(this.catId);\n            this.catId.forEach(function (item) {\n                _this.sendCategory.push({\n                    category_id: item,\n                    vendor_id: _this.vendorId,\n                });\n            });\n            console.log(this.sendCategory);\n\n            axios\n                .post(`/api/vendor/vendorsCategory`, _this.sendCategory)\n                .then(function (response) {\n                    window.location.reload();\n                })\n                .catch((error) => {\n                    _this.errors = error.response.data.errors;\n                    console.log(error.response.data.errors);\n                });\n        },\n        getvendorprofile() {\n            var _this = this;\n            axios\n                .get(`/wedding/vendorMasterList`)\n                .then(function (response) {\n                    console.log(response);\n                    _this.defaultCategories = response.data;\n                })\n                .catch((errors) => {\n                    console.log(\"Get All: \" + errors);\n                });\n        },\n    },\n    mounted() {\n        this.$nextTick(function () {\n            this.getvendorprofile();\n        });\n    },\n};\n</script>\n\n<style scoped>\n/* Nav Tab CSS Start*/\n\n/* Nav Tab CSS Ends */\n\n.errMsg {\n    color: red;\n    font-size: 10px;\n}\n.btn-container {\n    display: flex;\n    flex-direction: row;\n    justify-content: center;\n    margin: 1em 0;\n}\n.tab-btn.active {\n    background: #461952;\n    color: white !important;\n    background: linear-gradient(0deg, #c4456f, #c4456f),\n        linear-gradient(0deg, #c4456f, #c4456f), #c4456f;\n    border-radius: 58.0689px;\n    box-shadow: none;\n    border: none;\n    display: block;\n    cursor: pointer;\n    border-radius: 58.0689px;\n    width: 32px;\n    height: 4px;\n    margin: 0 3px;\n    box-shadow: 0px 0px 3px 1.5px #888888;\n}\n\n.ven-profile-form {\n    background: rgba(255, 255, 255, 0.8);\n    box-shadow: 1.97875px 1.97875px 1.97875px rgba(224, 219, 219, 0.25);\n    border-radius: 21.0451px;\n    margin-top: 5em;\n}\n#ven-profile-form-head {\n    font-family: Sahitya;\n    font-style: normal;\n    font-weight: normal;\n    font-size: 18.9267px;\n    line-height: 29px;\n    text-align: center;\n    text-transform: uppercase;\n    color: #461952;\n    text-shadow: 0px 1.51414px 1.51414px rgba(0, 0, 0, 0.25);\n    margin: 1em 0;\n}\n.vendor-logo-upload > label {\n    display: flex;\n    flex-direction: column;\n    width: max-content;\n    text-align: center;\n    margin: 0.5em auto;\n    position: relative;\n    cursor: pointer;\n}\n.vendor-logo-upload img {\n    width: 61px;\n    height: 61px;\n}\n.vendor-logo-upload span {\n    display: block;\n    font-family: Segoe UI;\n    font-style: normal;\n    font-weight: normal;\n    font-size: 8.67394px;\n    line-height: 9px;\n    color: #736e6e;\n    position: absolute;\n    bottom: 3px;\n    left: 22px;\n}\n\n#vendor-details > h1 {\n    font-family: Segoe UI;\n    font-style: normal;\n    font-weight: 600;\n    font-size: 17.1823px;\n    line-height: 18px;\n    text-align: center;\n    text-transform: capitalize;\n    color: #461952;\n}\n.vendor-details-form-fields > label {\n    display: flex;\n    flex-direction: column;\n    background: #ffffff;\n    border: 0.179498px solid #7e7979;\n    box-sizing: border-box;\n    border-radius: 3.29558px;\n    font-family: Segoe UI;\n    font-style: normal;\n    font-weight: normal;\n    font-size: 16.23896px;\n    color: #000000;\n    padding: 10px 20px;\n}\n.vendor-details-form-fields > label > input {\n    font-family: Segoe UI;\n    font-style: normal;\n    font-weight: normal;\n    font-size: 12.23896px;\n    line-height: 11px;\n    color: #afa5a5;\n    outline: none;\n    border: 0;\n    margin: 5px 0;\n    background-color: transparent !important;\n}\n\n.vendor-address-form-fields {\n    display: grid;\n    grid-template-columns: repeat(2, 1fr);\n    column-gap: 12px;\n    row-gap: 4px;\n}\n/* .vendor-address-form-fields > label:first-child {\n    grid-column-start: 1;\n    grid-column-end: -1;\n} */\n/* .vendor-address-form-fields > label:last-child {\n    grid-column-start: 1;\n    grid-column-end: -1;\n    position: relative;\n} */\n.vendor-address-form-fields > label {\n    display: flex;\n    flex-direction: column;\n    background: #ffffff;\n    border: 0.179498px solid #7e7979;\n    box-sizing: border-box;\n    border-radius: 3.29558px;\n    font-family: Segoe UI;\n    font-style: normal;\n    font-weight: normal;\n    font-size: 16.23896px;\n    color: #000000;\n    padding: 10px 20px;\n}\n#vendorCategories {\n    padding: 0px 25px 9px 0px;\n    margin-left: -4px;\n    border: none;\n    -webkit-appearance: none;\n    -moz-appearance: none;\n    appearance: none;\n    background: #ffffff;\n    border-radius: 5px;\n    background-image: url(\"/assets/vendors/dashboard/dropdown-arrow.svg\");\n    background-repeat: no-repeat;\n    background-position: 98%;\n    background-size: 15px;\n    border: none;\n    font-family: Segoe UI;\n    font-style: normal;\n    font-weight: normal;\n    font-size: 12.97492px;\n\n    /* identical to box height */\n\n    color: #7e7979;\n}\n#vendorCategories > option {\n    font-family: Segoe UI;\n    font-style: normal;\n    font-weight: normal;\n    font-size: 10px;\n    line-height: 13px;\n    color: #3d3838;\n}\n.vendor-address-form-fields > label > input {\n    font-family: Segoe UI;\n    font-style: normal;\n    font-weight: normal;\n    font-size: 12.23896px;\n    line-height: 11px;\n    color: #afa5a5;\n    outline: none;\n    border: 0;\n    margin: 5px 0;\n    background-color: transparent !important;\n}\n#location-link {\n    background: #d8839f;\n    box-shadow: 0px 0px 4px 0.1px rgba(0, 0, 0, 0.25);\n    border-radius: 14.3599px;\n    font-family: Segoe UI;\n    font-style: normal;\n    font-weight: 600;\n    font-size: 9.05367px;\n    line-height: 8px;\n    text-transform: capitalize;\n    color: #ffffff;\n    width: max-content;\n    padding: 5px 15px;\n    outline: none;\n    border: 0;\n    right: 10px;\n    position: absolute;\n}\n\n#ven-profile-form-submit {\n    position: relative;\n    margin: 1.5em 0;\n    padding: 5em;\n}\n\n#ven-profile-form-submit > button {\n    background: #c4456f;\n    box-shadow: 0px 0px 4px 1px rgba(0, 0, 0, 0.25);\n    border-radius: 22.4987px;\n    padding: 7px 30px;\n    position: absolute;\n    right: 15px;\n    top: 40px;\n    margin-left: auto;\n    font-family: Segoe UI;\n    font-style: normal;\n    font-weight: 600;\n    font-size: 11.2493px;\n    line-height: 15px;\n    text-transform: capitalize;\n    color: #ffffff;\n    outline: none;\n    border: 0;\n}\n\n@media screen and (max-width: 762px) {\n    .vendor-details-form-fields {\n        grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));\n    }\n    .vendor-address-form-fields {\n        grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));\n    }\n    .vendor-preview-form-fields {\n        grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));\n    }\n}\n</style>\n"],"sourceRoot":""}]);
